@import url('https://fonts.googleapis.com/css2?family=Merriweather+Sans&family=Poppins:wght@300&family=Secular+One&display=swap');

body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color:rgb(19, 2, 117);
  
}
.index_main{
  font-size: 3rem;
  color: cyan;
  font-weight:  800;
  font-family: 'Merriweather Sans', sans-serif;
}

.main_heading{
  text-align: center;
  padding: 1rem;
  margin: 1rem;
  background-color: rgb(91, 63, 245);
  border-radius: 5px;
}


.text h1{
  font-family: 'Secular One', sans-serif;
  font-size: 4.5rem;
  color: white;

}

.text p{
  font-family: 'Secular One', sans-serif;
  color: white;
  font-size: 1.5rem;
}
.image1 img{
  float: left;
  margin: 2rem;
  width: 15%;
  animation: rotation infinite 10s linear;

}
.image2 img{
  float: right;
  width: 15%;
  margin: 2rem;
  animation: rotation infinite 10s linear;


}

@keyframes rotation{
  from{
    transform:rotate(0deg);
  }

  to{
    transform:rotate(360deg);
  }
}

.particle{
  position: absolute;
  z-index: -1;
  width: 100%;
}

.body{

  text-align: left;
  padding: 1rem;
  margin: 3rem;
  background-color: rgb(85, 102, 255);
  border-radius: 5px;

}

.searchkabox{
  margin: 1rem;
  text-align: center; 
}

.searchkabox .searchbar{
  background-color:rgb(225, 228, 255);
  padding: 1rem;
  width: 98%;
  outline: none;
  border-radius:5px;
  border-color: white;
}

.searchkabox .searchbar::placeholder{
  font-weight: bold;
  color: rgb(82, 82, 82);
  font-size: 1rem;
}

.detailbox{
  position: relative;
  margin: 3rem;
  text-align: left;
  
  
}
.detailbox h2{
  color: white;
  font-family: 'Merriweather Sans', sans-serif;
  font-size: 3rem;
  border-bottom: 2px solid whitesmoke;
  
}

.planimg{
  float: right;
  

}

.planimg img{
  width:400px;
  height: 400px;
  margin: 6rem;
  animation: rotation infinite 15s linear;
 
  
}
.deepbox{
  position: relative;
  margin: 3rem;
  text-align: left;
  
  
}

.deepbox h3{
  color: white;
  font-family: Poppins;
  font-size: 1.5rem;
  font-weight: 300;

}

.deepbox p{
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  text-align: justify; 
}

.end h2{
  font-size: 1.5rem;
  color: white;
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}

.end a{
  color :rgb(251, 255, 0);
}
