body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color:rgb(19, 2, 117);
  
}
.index_main{
  font-size: 3rem;
  color: cyan;
  font-weight:  800;
  font-family: 'Merriweather Sans', sans-serif;
}

.main_heading{
  text-align: center;
  padding: 1rem;
  margin: 1rem;
  background-color: rgb(91, 63, 245);
  border-radius: 5px;
}


.text h1{
  font-family: 'Secular One', sans-serif;
  font-size: 4.5rem;
  color: white;

}

.text p{
  font-family: 'Secular One', sans-serif;
  color: white;
  font-size: 1.5rem;
}
.image1 img{
  float: left;
  margin: 2rem;
  width: 15%;
  animation: rotation infinite 10s linear;

}
.image2 img{
  float: right;
  width: 15%;
  margin: 2rem;
  animation: rotation infinite 10s linear;


}

@keyframes rotation{
  from{
    transform:rotate(0deg);
  }

  to{
    transform:rotate(360deg);
  }
}

.particle{
  position: absolute;
  z-index: -1;
  width: 100%;
}

.body{

  text-align: left;
