  margin: 1rem;
  text-align: center; 
}

.searchkabox .searchbar{
  background-color:rgb(225, 228, 255);
  padding: 1rem;
  width: 98%;
  outline: none;
  border-radius:5px;
  border-color: white;
}

.searchkabox .searchbar::-webkit-input-placeholder{
  font-weight: bold;
  color: rgb(82, 82, 82);
  font-size: 1rem;
}

.searchkabox .searchbar:-ms-input-placeholder{
  font-weight: bold;
  color: rgb(82, 82, 82);
  font-size: 1rem;
}

.searchkabox .searchbar::placeholder{
  font-weight: bold;
  color: rgb(82, 82, 82);
  font-size: 1rem;
}

